"use client";

import { useState } from "react";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Loader2, Trash } from "lucide-react";
import { toast } from "sonner";
import { generateCoverLetter, getCoverLetters, deleteCoverLetter } from "@/actions/coverletter";

export default function AICoverLetterPage() {
  const [input, setInput] = useState({
    jobTitle: "",
    companyName: "",
    jobDescription: "",
  });
  const [coverLetters, setCoverLetters] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    setInput({ ...input, [e.target.name]: e.target.value });
  };

  const handleGenerate = async () => {
    if (!input.jobTitle || !input.companyName || !input.jobDescription) {
      toast.error("Please fill all fields.");
      return;
    }
    setLoading(true);
    try {
      const letter = await generateCoverLetter(input);
      setCoverLetters((prev) => [letter, ...prev]);
      toast.success("Cover letter generated!");
    } catch (error) {
      console.error(error);
      toast.error(error.message || "Failed to generate cover letter");
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    try {
      await deleteCoverLetter(id);
      setCoverLetters((prev) => prev.filter((l) => l.id !== id));
      toast.success("Cover letter deleted!");
    } catch (error) {
      console.error(error);
      toast.error(error.message || "Failed to delete cover letter");
    }
  };

  return (
    <div className="space-y-6 p-6 max-w-4xl mx-auto">
      <h1 className="text-4xl font-bold gradient-title text-center">
        AI Cover Letter Generator
      </h1>

      <div className="space-y-4">
        <input
          type="text"
          name="jobTitle"
          placeholder="Job Title"
          value={input.jobTitle}
          onChange={handleChange}
          className="w-full border rounded p-2"
        />
        <input
          type="text"
          name="companyName"
          placeholder="Company Name"
          value={input.companyName}
          onChange={handleChange}
          className="w-full border rounded p-2"
        />
        <Textarea
          name="jobDescription"
          placeholder="Enter job description or requirements..."
          value={input.jobDescription}
          onChange={(e) => handleChange({ target: { name: "jobDescription", value: e.target.value } })}
          className="h-40"
        />
      </div>

      <Button onClick={handleGenerate} disabled={loading}>
        {loading ? (
          <>
            <Loader2 className="h-4 w-4 animate-spin mr-2" />
            Generating...
          </>
        ) : (
          "Generate Cover Letter"
        )}
      </Button>

      {coverLetters.length > 0 && (
        <div className="space-y-4 mt-6">
          {coverLetters.map((letter) => (
            <div key={letter.id} className="border rounded-lg p-4 bg-muted/50 relative">
              <Button
                variant="destructive"
                size="sm"
                className="absolute top-2 right-2"
                onClick={() => handleDelete(letter.id)}
              >
                <Trash className="h-4 w-4" />
              </Button>
              <pre className="whitespace-pre-wrap">{letter.content}</pre>
              <p className="mt-2 text-sm text-muted-foreground">
                {letter.jobTitle} at {letter.companyName}
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
